:root {
  font-family: 'Raleway';
  font-variant-numeric: lining-nums;
  color: var(--text-clr-000);
  font-size: 16px;
  --main-clr: hsl(260, 100%, 60%);
  --bg-clr-700: hsl(0, 0%, 24%);
  --bg-clr-800: hsl(0, 0%, 4%);
  --bg-clr-900: hsl(0, 0%, 8%);
  --text-clr-000: hsl(0, 0%, 100%);
  --border-radius: 0.25rem;
  --transition-timing: 0.3s;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  font-weight: 500;
  position: relative;
  text-decoration: none;
  color: var(--main-clr);
}

a::after {
  position: absolute;
  content: '';
  width: 0;
  height: 1px;
  background: var(--main-clr);
  left: 0;
  bottom: -0.25rem;
  transition: width var(--transition-timing) ease-in-out;
}

a:hover::after {
  width: 100%;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 700;
}

button {
  font-family: 'Raleway';
  font-variant-numeric: lining-nums;
  cursor: pointer;
  appearance: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: var(--bg-clr-900);
  border: 1px solid var(--main-clr);
  border-radius: var(--border-radius);
  font-size: 1rem;
  color: var(--main-clr);
  transition: background var(--transition-timing) ease-in-out,
  border var(--transition-timing) ease-in-out,
  color var(--transition-timing) ease-in-out;
}

button:not(:disabled):hover {
  background: var(--main-clr);
  color: var(--bg-clr-900);
}

button:disabled {
  cursor: not-allowed;
  border: 1px solid var(--bg-clr-700);
  color: var(--bg-clr-700);
}

#root {
  min-height: 100svh;
  display: grid;
  grid-template-rows: max-content auto max-content;
}

#root > * {
  width: 100%;
}

.preloader {
  height: 100%;
  display: grid;
  place-items: center;
}

.preloader > div {
  position: relative;
  width: 4rem;
  aspect-ratio: 1;
  border-radius: 100%;
  background: linear-gradient(90deg, var(--main-clr) 10%, rgba(255, 255, 255, 0) 50%);
  animation: preloader 1s infinite ease-in-out;
}

.preloader > div::before {
  position: absolute;
  content: '';
  width: 2rem;
  aspect-ratio: 1;
  border-radius: 100% 0 0 0;
  background: var(--main-clr);
}

.preloader > div::after {
  position: absolute;
  content: '';
  inset: 0;
  margin: auto;
  width: 3rem;
  aspect-ratio: 1;
  border-radius: 100%;
  background: var(--bg-clr-800);
}

@keyframes preloader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.container {
  margin: 0 auto;
  max-width: 1600px;
  height: 100%;
  padding: 1rem 2rem;
}

.logo {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-weight: 700;
}

.logo > img {
  height: 2rem;
}

.footer-div, .nav-div {
  background: var(--bg-clr-900);
}

.footer, .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-div {
  background: var(--bg-clr-800);
}

.goods-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.25rem;
}

.goods-item {
  padding: 1.5rem;
  background: var(--bg-clr-900);
  display: grid;
  border-radius: var(--border-radius);
  grid-template-rows: subgrid;
  grid-row: span 4;
}

.goods-item > img {
  width: 100%;
  aspect-ratio: 1;
  object-fit: contain;
  background: var(--main-clr);
  border-radius: var(--border-radius);
}

.goods-item-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.goods-item-price > span, .cart-list-price {
  font-weight: 700;
  color: var(--main-clr);
}

.cart {
  z-index: 1;
  position: fixed;
  inset: auto 1rem 1rem auto;
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 0.5rem;
}

.cart-list {
  transform: translateX(4rem);
  display: flex;
  gap: 2rem;
  flex-direction: column;
  border-radius: var(--border-radius);
  border: 1px solid var(--bg-clr-700);
  background: var(--bg-clr-900);
}

.svg-button {
  display: flex;
  cursor: pointer;
  color: var(--main-clr);
  align-items: center;
  justify-content: center;
}

.cart-list-items {
  position: relative;
  display: grid;
  gap: 1.75rem;
  overflow-y: auto;
}

.cart-list-items::-webkit-scrollbar {
  display: none;
}

.cart-list-sum {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.cart-item {
  align-items: center;
  display: grid;
  grid-template-columns: subgrid;
  grid-column: span 3;
}

.cart-item > div {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.cart-item-name {
  font-weight: 700;
}

.cart-item-control {
  height: 2rem;
  width: 6rem;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
}

.cart-item-control > * {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart-item-button {
  padding: 0;
}

.alert {
  transform: translateX(-4rem);
  z-index: 1;
  position: fixed;
  inset: auto auto 1rem 1rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  border-radius: var(--border-radius);
  border: 1px solid var(--bg-clr-700);
  background: var(--bg-clr-900);
}

.cart-list, .alert {
  padding: 0 1.5rem;
  max-height: 0;
  pointer-events: none;
  opacity: 0;
  transition: padding var(--transition-timing) ease-in-out,
  opacity var(--transition-timing) ease-in-out,
  transform var(--transition-timing) ease-in-out,
  max-height var(--transition-timing) ease-in-out;
}

.cart-list.shown, .alert.shown {
  padding: 1.5rem;
  max-height: 75vh;
  transform: translateX(0);
  pointer-events: all;
  opacity: 1;
}

@media (width < 550px) {
  .cart-item-name {
    grid-column: span 2;
  }

  .cart-item {
    grid-column: span 2;
    grid-row: span 2;
    gap: 0.5rem;
  }
}